<template>
  <div class="step_one">
    <div class="container">
      <div class="front">
        {{ $store.state.Front.title }}
        <input type="checkbox" v-model="$store.state.Front.boolean" />
      </div>
      <div class="back">
        {{ $store.state.Back.title }}
        <input type="checkbox" v-model="$store.state.Back.boolean" />
      </div>
    </div>
    <div class="nav">
      <router-link to="/">
        <button>← Wstecz</button>
      </router-link>
      <div class="right_nav">
        <button v-if="!pickOne()">Wybierz stronę</button>
        <router-link v-if="pickOne()" to="/home/step_two">
          <button>Dalej →</button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  name: 'StepOne',
  data: () => ({
    frontChecked: false,
    backChecked: false
  }),
  methods: {
    ...mapMutations([
      'updatePrice'
    ]),
    pickOne () {
      return this.$store.state.Front.boolean || this.$store.state.Back.boolean
    }
  },
  updated () {
    this.updatePrice(0)
  }
}
</script>
